<template>
	<view class="sugicaldetail">
		<view style="height: 90rpx;">
			<view class="toptitle">
				<text>{{titleName||'未填写'}}</text>
			</view>
		</view>
		<scroll-view :scroll-top="scrollTop" scroll-y="true" class="scroll-Y" @scrolltoupper="upper" @scrolltolower="lower"
		@scroll="scroll">
			<swiper v-if="methodsBanner.length!=0" :indicator-dots="true" :autoplay="Imageplay" :interval="3000" :duration="1000" indicator-color="#333333" indicator-active-color="#ffffff" circular="true">
				<swiper-item  @tap='PreviewPic(index,methodsBanner)' v-for="(item,index) in methodsBanner" :key='index'>
					<view class="swiper-item">
						<image :src=item mode="aspectFit"></image>
					</view>
				</swiper-item>	
			</swiper>
			<swiper v-else :indicator-dots="true" :autoplay="Imageplay" :interval="3000" :duration="1000" indicator-color="#333333" indicator-active-color="#ffffff" circular="true">
				<swiper-item>
					<view class="swiper-item">
						<image src='../../static/image/default.png' mode=""></image>
					</view>
				</swiper-item>
			</swiper>	
