				<!-- 医生信息 -->
				<view class="docInfor">
					<image v-if="docinfor.avatarLinks" :src=docinfor.avatarLinks mode=""></image>
					<image v-else src='../../static/image/shoyiilogo.png' mode=""></image>
					<view class="docInfor_content">
						<view class="">
							<text class="doctor">{{surcigal.doctorName||'无数据'}}</text>
							<view class="infor">
								<text>{{docinfor.departmentName||'无数据'}}</text>
								<text class="line">|</text>
								<text>{{docinfor.hospital||'无数据'}}</text>
							</view>
						</view>
						<view class="infor">
							<text>{{surcigal.createTime|timeStamp}}</text>
						</view>
					</view>
				</view>
				<!-- introduce -->
				<view class="introduce">
					<view class="example-body">
						<text>手术设计介绍</text>
					</view>
					<view class="content">
						<view class="text1">
							<text>病种类别：</text>
							<text v-if="surcigal.diseaseCategoryName">{{surcigal.diseaseCategoryName||'暂无'}}</text>
							<text v-else>暂无数据</text>
						</view>
						<view class="text1">
							<text>材料应用：</text>
							<text v-if="surcigal.materials">{{surcigal.materials.name||'暂无'}}</text>
							<text v-else>暂无数据</text>
						</view>
						<view class="text1">
							<text>方法说明：</text>
							<text v-if="surcigal.title">{{surcigal.title||'暂无数据'}}</text>
							<text v-else>暂无数据</text>
						</view>
					</view>
				</view>
				<view class="introduce">
					<view class="example-body">
						<text>数字化3D打印导板</text>
					</view>
					<view class="content">
						<view v-if="surcigal.threeDGuideList" class="" v-for="(item,index) in surcigal.threeDGuideList" :key='index'>
							<view class="" style="display: flex;align-items: center;">
								<view class="indexDot" style="margin-right: 20rpx;width: 16rpx;height: 16rpx;background: #86B0D4;border-radius: 50%;"></view>
								<view class="" style="color: #000000;font-size: 28rpx;">
									<text>{{index+1}}</text>
									<text>. {{item.name}}</text>
								</view>
							</view>
							<view class="" style="display: flex;justify-content: space-around;flex-wrap: wrap;">
								<view class="" style="width: 210rpx;height: 210rpx;margin: 10rpx 20rpx;border: 1px solid #EEEEEE;"  v-for="(itemImage,index) in item.previewImage" :key='index'>
									<image @click="PreviewPic(index,item.previewImage)" :src="itemImage" mode="aspectFit" style="margin: 0 auto;width: 100%;height: 100%;"></image>
								</view>
							</view>
						</view>
						<view class="" v-else>
							暂无数据
						</view>
					</view>
				</view>
				<!-- 数字化3d打印器械 -->
				<view class="introduce">
					<view class="example-body">
						<text>数字化3D打印器械</text>
					</view>
					<view class="content">
						<view v-if="surcigal.threeDGuideList" class="" v-for="(item,index) in surcigal.threeDInstrumentList" :key='index'>
							<view class="" style="display: flex;align-items: center;">
								<view class="indexDot" style="margin-right: 20rpx;width: 16rpx;height: 16rpx;background: #86B0D4;border-radius: 50%;"></view>
								<view class="" style="color: #000000;font-size: 28rpx;">
									<text>{{index+1}}</text>
									<text>. {{item.name}}</text>
								</view>
							</view>
							<view class="" style="display: flex;justify-content: space-around;flex-wrap: wrap;">
								<view class="" style="width: 210rpx;height: 210rpx;margin: 10rpx 20rpx;border: 1px solid #EEEEEE;"  v-for="(itemImage,index) in item.previewImage" :key='index'>
									<image @click="PreviewPic(index,item.previewImage)" :src="itemImage" mode="aspectFit" style="margin: 0 auto;width: 100%;height: 100%;"></image>
								</view>
							</view>
						</view>
						<view class="" v-else>
							暂无数据
						</view>
					</view>
				</view>
				<!-- 手术方法步骤 -->
				<view class="introduce">
					<view class="example-body">
						<text>手术方法步骤</text>
					</view>
					<view class="content">
						<view v-if="surcigal.threeDGuideList" class="" v-for="(item,index) in surcigal.surgicalDesignStepsList" :key='index'>
							<view class="" style="display: flex;align-items: center;">
								<view class="indexDot" style="margin-right: 20rpx;width: 16rpx;height: 16rpx;background: #86B0D4;border-radius: 50%;"></view>
								<view class="" style="color: #000000;font-size: 28rpx;">
									<text>{{index+1}}</text>
									<text>. {{item.name}}</text>
								</view>
								
							</view>
							<view class="" style="display: flex;justify-content: space-around;flex-wrap: wrap;">
								<view class="" style="width: 210rpx;height: 210rpx;margin: 10rpx 20rpx;border: 1px solid #EEEEEE;"  v-for="(itemImage,index) in item.previewImage" :key='index'>
									<image @click="PreviewPic(index,item.previewImage)" :src="itemImage" mode="aspectFit" style="margin: 0 auto;width: 100%;height: 100%;"></image>
								</view>
							</view>
						</view>
						<view class="" v-else>
							暂无数据
						</view>
					</view>
				</view>
				<!-- 术前情况 -->
				<view class="introduce">
					<view class="example-body">
						<text>术前情况</text>
					</view>
					<view class="content">
						<view v-if="surcigal.threeDGuideList" class="" v-for="(item,index) in surcigal.preoperativeConditionList" :key='index'>
							<view class="" style="display: flex;align-items: center;">
								<view class="indexDot" style="margin-right: 20rpx;width: 16rpx;height: 16rpx;background: #86B0D4;border-radius: 50%;"></view>
								<view class="" style="color: #000000;font-size: 28rpx;">
									<text>{{index+1}}</text>
									<text>. {{item.name}}</text>
								</view>
								
							</view>
							<view class="" style="display: flex;justify-content: space-around;flex-wrap: wrap;">
								<view class="" style="width: 210rpx;height: 210rpx;margin: 10rpx 20rpx;border: 1px solid #EEEEEE;"  v-for="(itemImage,index) in item.previewImage" :key='index'>
									<image @click="PreviewPic(index,item.previewImage)" :src="itemImage" mode="aspectFit" style="margin: 0 auto;width: 100%;height: 100%;"></image>
								</view>
							</view>
						</view>
						<view class="" v-else>
							暂无数据
						</view>
					</view>
				</view>
				<!-- 术后情况 -->
				<view class="introduce">
					<view class="example-body">
						<text>术后情况</text>
					</view>
					<view class="content">
						<view v-if="surcigal.threeDGuideList" class="" v-for="(item,index) in surcigal.postoperativeSituationList" :key='index'>
							<view class="" style="display: flex;align-items: center;">
								<view class="indexDot" style="margin-right: 20rpx;width: 16rpx;height: 16rpx;background: #86B0D4;border-radius: 50%;"></view>
								<view class="" style="color: #000000;font-size: 28rpx;">
									<text>{{index+1}}</text>
									<text>. {{item.name}}</text>
								</view>
							</view>
							<view class="" style="display: flex;justify-content: space-around;flex-wrap: wrap;">
								<view class="" style="width: 210rpx;height: 210rpx;margin: 10rpx 20rpx;border: 1px solid #EEEEEE;"  v-for="(itemImage,index) in item.previewImage" :key='index'>
									<image @click="PreviewPic(index,item.previewImage)" :src="itemImage" mode="aspectFit" style="margin: 0 auto;width: 100%;height: 100%;"></image>
								</view>
							</view>
						</view>
						<view class="" v-else>
							暂无数据
						</view>
					</view>
				</view>
				<!-- word -->
				<view class="introduce">
					<view class="example-body">
						<text>ppt</text>
					</view>
					<view class="content">
						<view v-if="surcigal.wordPdfIds" class="example-body" style="justify-content: center;">
							<view class="tag-view" >
								<uni-tag  text="点击预览word" @click="previewword(surcigal.loadLink[surcigal.wordPdfIds])" />
							</view>
						</view>
						<view class="" v-else>
							暂无数据
						</view>
					</view>
				</view>
				<!-- ppt -->
				<view class="introduce">
					<view class="example-body">
						<text>word</text>
					</view>
					<view class="content">
						<view v-if="surcigal.pptPdfIds" class="example-body" style="justify-content: center;">
							<view class="tag-view" >
								<uni-tag  text="点击预览ppt" @click="previewppt(surcigal.loadLink[surcigal.pptPdfIds])" />
							</view>
						</view>
						<view class="" v-else>
							暂无数据
						</view>
					</view>
				</view>
				<!-- 视频描述 -->
				<view class="introduce">
					<view class="example-body">
						<text>视频描述</text>
					</view>
					<view class="content" style="text-align: center;">
						<video v-if="surcigal.videoModelIds" :src="surcigal.loadLink[surcigal.videoModelIds]" controls></video>
						<view class="" v-else>
							暂无视频数据
						</view>
					</view>
				</view>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				Imageplay:true,//轮播图是否自动切换,true自动切换
				scrollTop: 0,
				old: {
					scrollTop: 0
				},	
			}
		},
		methods:{
			upper: function(e) {
				// console.log(e)
			},
			lower: function(e) {
				// console.log(e)
			},
			scroll: function(e) {
				// console.log(e)
				this.old.scrollTop = e.detail.scrollTop
			},
			// 预览图片
			PreviewPic(index,data){
				console.log(index)
				console.log(data)
				console.log(Object.values(data))
				var arr=Object.values(data)
				uni.previewImage({
					current:index,//预览的第一张图，可以使index，可以是路径
					urls:arr,//必须是数组
					indicator:'number',
					loop:true,
					longPressActions:{
						 itemList: ['保存图片'],//长按时弹出的列表选项
						 success: function(data) {
							console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
							uni.saveImageToPhotosAlbum({
								filePath:arr[data.index + 1],
								success(e){
									console.log(e)
									uni.showToast({
										title:'保存成功',
										icon:'none'
									})
								},
								fail(e){
									uni.showToast({
										title:e.errMsg
									})
								}
							})
						 },
						 fail: function(err) {
						 }
					},
					success(e){
						console.log(e)
					}
				})
			},
			// 预览word
			previewword(url){
				// console.log(encodeURIComponent(url))
				var url=encodeURIComponent(url)
				uni.navigateTo({
					url:'/pages/previewPdf/previewPdf?pdfUrl='+url,
					animationType:'pop-in'
				})
			},
			// 预览ppt
			previewppt(url){
				// console.log(encodeURIComponent(url))
				var url=encodeURIComponent(url)
				uni.navigateTo({
					url:'/pages/previewPdf/previewPdf?pdfUrl='+url,
					animationType:'pop-in'
				})
			},
		},
		props:{
			surcigal:{
				type:Object,
				default:()=>{
					return{}
				}
			},
			docinfor:{
				type:Object,
				default:()=>{
					return{}
				}
			},
			// 轮播图
			methodsBanner:{
				type:Array,
				default:()=>{
					return[]
				}
			},
			titleName:{
				type:String,
				default:''
			}
		},
		created() {
			console.log(this.surcigal)
		}
	
	}
</script>

